import React, {useState,useEffect} from 'react'
import Layout from '../../components/Layout/Layout'
import AdminMenu from '../../components/Layout/AdminMenu'
import axios from 'axios'
import toast from 'react-hot-toast'
import { useNavigate } from 'react-router-dom'
import { Select } from 'antd'
const {Option} = Select

const CreateProduct = () => {
  const navigate = useNavigate()
  const [name,setName] = useState('')
  const [price,setPrice] = useState('')
  const [description,setDescription] = useState('')
  const [categories,setCategories] = useState([])
  const [category,setCategory] = useState('')
  const [quantity,setQuantity] = useState('')
  const [shipping,setShipping] = useState('')
  const [photo,setPhoto] = useState('')


  const getAllCategories = async () => {
    try {
      const {data} = await axios.get('/api/v1/category/get-category')
      if(data?.success){
        setCategories(data.categories)
      }
    } catch (error) {
      console.log(error)
      toast.error("Something went wrong in getting categories")
    }
  }
  useEffect(()=>{
    getAllCategories();
  },[])

  // function to create product
  const handleCreate = async(e) =>{
    e.preventDefault()
    try {
      const productData = new FormData()
      productData.append('name',name)
      productData.append('price',price)
      productData.append('description',description)
      productData.append('category',category)
      productData.append('quantity',quantity)
      productData.append('photo',photo)
      const {data} = axios.post('/api/v1/product/create-product',productData)
      if(data?.success){
        toast.error(data?.message)
      }
      else{
        toast.success("Product created successfully")
        navigate('/dashboard/admin/products')
      }
    } catch (error) {
      console.log(error)
      toast.error("Something went wrong in creating product")
    }
  }

  return (
    <Layout title={"Dashboard - Create Products"}>
      <div className="container-fluid m-3 p-3">
      <div className="row">
        <div className="col-md-3">
          <AdminMenu/>
        </div>
        <div className="col-md-9">
          <h1>Create products</h1>
          <div className="m-1 w-75">
            <Select bordered={false} placeholder="Select a category" suffixIcon={null} showSearch className='form-select mb-3' onChange={(value)=>{setCategory(value)}}>
              {categories.map((c)=>(
                <Option key={c._id} value={c._id}>
                  {c.name}
                </Option>
              ))}
            </Select>
            <div className="mb-3">
              

              <div className="mb-3">
                <input type="text" value={name} placeholder='write a name' className='form-control' onChange={(e)=>setName(e.target.value)} />
              </div>
              <div className="mb-3">
                <input type="text" value={description} placeholder='write a description' className='form-control' onChange={(e)=>setDescription(e.target.value)} />
              </div>
              <div className="mb-3">
                <input type="number" value={price} placeholder='write a price' className='form-control' onChange={(e)=>setPrice(e.target.value)} />
              </div>
              <div className="mb-3">
                <input type="number" value={quantity} placeholder='write a quantity' className='form-control' onChange={(e)=>setQuantity(e.target.value)} />
              </div>
              <div className="mb-3">
                <Select bordered={false} placeholder="Select Shiping" size='large' showSearch className='form-select mb-3' suffixIcon={null} onChange={(value)=>setShipping(value)}>
                  <option value="No">No</option>
                  <option value="Yes">Yes</option>
                </Select>
              </div>

              <div className="mb-3">
                <label className="btn btn-outline-secondary col-md-12 mb-10">
                  {photo?photo.name:'Upload photo'}
                  <input type="file" name='photo' accept='image/*' onChange={(e)=>setPhoto(e.target.files[0])} hidden />
                </label>
              </div>
              <div className="mb-3">
                {photo && (
                  <div className="text-center">
                    <img src={URL.createObjectURL(photo)} alt="product_photo" height={'200px'} className='img img-responsive'/>
                  </div>
                )}
              </div>

              <div className="mb-3">
                <button className='btn btn-primary btn-dark' onClick={handleCreate}>Create Product</button>
              </div>

            </div>
          </div>
        </div>
      </div>
      </div>
    </Layout>
  )
}

export default CreateProduct
